.annotation-item-card {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    gap: 4px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #d5c8c8;
    cursor: pointer;
    --card-bg: white;
}

.annotation-item-card:hover {
    --card-bg: #f7f7f7;
    background-color: #f7f7f7;
}

.annotation-item-card-open {
    --card-bg: #e8f0ff;
    background-color: #e8f0ff;
}

.annotation-item-card-open:hover {
    --card-bg: #e8f0ff;
    background-color: #e8f0ff;
}

.annotation-item-card .header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    gap: 4px;
    color: #E6D7D9;
}

.annotation-item-card .header .title {
    color: #C4C4C4;
    font-size: 18px;
}

.annotation-item-card main {
    max-height: 10rem;
    overflow: hidden;
    position: relative;
}

.annotation-item-card main::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: -webkit-gradient(linear,left top, left bottom,color-stop(2rem, transparent),color-stop(10rem, var(--card-bg)));
    background: linear-gradient(transparent 2rem,var(--card-bg) 10rem);
}

.annotation-item-card main::before:hover {
    background-color: none;
}

.annotation-item-card .format-html {
    padding-left: 6px;
}

